import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    viewing: null,
    cart: [
      {
        id: 4,
        title: '1 Dozen White Lilies',
        description: 'A round bouquet of 1 dozen white lilies',
        price: 3150,
        images: [
          'flower_4.jpg'
        ]
      }
    ]
  },
  mutations: {
    addOrder (state) {
      state.cart.push(state.viewing);
    },
    setViewing (state, product) {
      state.viewing = product
    }
  },
  getters: {
    drawer: state => state.cart,
    viewing: state => state.viewing
  },
  actions: {

  }
})
